import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/layout/Home.vue';
import Login from '../views/layout/Login.vue';
import store from '../store';
import getMenuRoute from '../utils/permission';

Vue.use(VueRouter);

// eslint-disable-next-line no-unused-vars
const asyncRouterMap = [{
  path: '/product',
  name: 'Product',
  meta: {
    title: '商品',
    hidden: false,
    icon: 'profile',
  },
  component: Home,
  children: [{
    path: 'list',
    name: 'ProductList',
    meta: {
      title: '商品列表',
      hidden: false,
      icon: 'menu',
    },
    component: () => import('@/views/layout/pages/productList.vue'),
  }, {
    path: 'add',
    name: 'ProductAdd',
    meta: {
      title: '添加商品',
      hidden: false,
      icon: 'plus-square',
    },
    component: () => import('@/views/layout/pages/productAdd.vue'),
  }, {
    path: 'edit/:id',
    name: 'ProductEdit',
    meta: {
      title: '编辑商品',
      hidden: true,
      icon: 'plus-square',
    },
    component: () => import('@/views/layout/pages/productAdd.vue'),
  }, {
    path: 'category',
    name: 'Category',
    meta: {
      title: '类目管理',
      hidden: false,
      icon: 'shopping-cart',
    },
    component: () => import('@/views/layout/pages/category.vue'),
  }],
}];

const routes = [
  {
    path: '/',
    name: 'Home',
    // redirect: '/index',
    redirect: {
      path: '/index',
    },
    meta: {
      title: '首页',
      hidden: false,
      icon: 'home',
    },
    component: Home,
    children: [{
      path: '/index',
      name: 'Index',
      meta: {
        title: '统计',
        hidden: false,
        icon: 'bar-chart',
      },
      component: () => import('@/views/layout/pages/index.vue'),
    },
    ],
  },
  {
    path: '/login',
    name: 'Login',
    meta: {
      title: '登录',
      hidden: true,
    },
    component: Login,
  },
];

const router = new VueRouter({
  routes,
});
let isRoutesAdd = false;
router.beforeEach((to, from, next) => {
  if (to.path !== '/login') {
    if (store.state.user.appkey && store.state.user.username && store.state.user.email) {
      if (!isRoutesAdd) {
        const menuRoutes = getMenuRoute(store.state.user.role, asyncRouterMap);
        store.dispatch('changeMenuRoutes', routes.concat(menuRoutes)).then(() => {
          router.addRoutes(menuRoutes);
          next();
        });
        isRoutesAdd = true;
      }
      return next();
    }
    return next('/login');
  }
  return next();
});
export default router;
