<template>
    <div class="login">
        <a-form-model class="login-form"
                      ref="loginForm"
                      :model="loginForm"
                      :rules="rules" v-bind="layout"
        >
          <a-form-model-item has-feedback label="Email" prop="email">
            <a-input v-model="loginForm.email" aria-placeholder="请输入邮箱"/>
          </a-form-model-item>
          <a-form-model-item has-feedback label="Password" prop="password">
            <a-input v-model="loginForm.password" type="password"
                     autocomplete="off" aria-placeholder="请输入密码"
                      @keyup.enter="submitForm('loginForm')"/>
          </a-form-model-item>
          <a-form-model-item :wrapper-col="{ span: 14, offset: 4 }">
            <a-button type="primary" @click="submitForm('loginForm')">
              登录
            </a-button>
            <a-button style="margin-left: 10px" @click="resetForm('loginForm')">
              重置
            </a-button>
          </a-form-model-item>
        </a-form-model>
    </div>
</template>

<script>
// eslint-disable-next-line no-unused-vars,import/extensions
import api from '@/api/user.js';

export default {
  name: 'Login',
  data() {
    /// ^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
    // eslint-disable-next-line no-unused-vars,no-useless-escape
    const emailReg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
    // eslint-disable-next-line consistent-return
    const checkEmail = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('请输入邮箱'));
      }
      // eslint-disable-next-line no-empty
      if (emailReg.test(value)) {
        return callback();
      }
      return callback(new Error('邮箱格式错误'));
    };
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        callback();
      }
    };
    return {
      loginForm: {
        password: '',
        email: '',
      },
      rules: {
        password: [{ validator: validatePass, trigger: 'change' }],
        email: [{ validator: checkEmail, trigger: 'change' }],
      },
      layout: {
        labelCol: { span: 4 },
        wrapperCol: { span: 14 },
      },
    };
  },
  methods: {
    submitForm(formName) {
      // eslint-disable-next-line consistent-return
      this.$refs[formName].validate((valid) => {
        if (valid) {
          api.login(this.loginForm).then((res) => {
            this.$store.dispatch('setUserInfo', res);
            this.$router.push({
              name: 'Home',
            });
          }).catch((error) => {
            this.$message.error(error);
          });
          return true;
        }
        throw new Error('error submit!!');
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

<style lang="less">
  @import url('~@/assets/css/login.less');
</style>
