/**
 * 存储的是 角色对应的路由
 * @type {{admin: [{name: string}, {name: string},
 * {name: string}], coustomer: [{name: string}, {name: string}]}}
 */
const roleToroute = {
  coustomer: [{
    name: 'Product',
  },
  {
    name: 'ProductList',
  },
  {
    name: 'ProductAdd',
  },
  {
    name: 'ProductEdit',
  }],
  admin: [{
    name: 'Product',
  },
  {
    name: 'ProductList',
  },
  {
    name: 'ProductAdd',
  },
  {
    name: 'Category',
  }, {
    name: 'ProductEdit',
  }],
};

export default function getMenuRoute(role, routes) {
  const allowRoutesName = roleToroute[role].map((item) => item.name);
  // eslint-disable-next-line array-callback-return,consistent-return
  const resultRoutes = routes.filter((r) => {
    const obj = r;
    if (allowRoutesName.indexOf(r.name) !== -1) {
      const { children } = obj;
      // eslint-disable-next-line no-param-reassign
      obj.children = children.filter((c) => allowRoutesName.indexOf(c.name) !== -1);
      return true;
    }
    return false;
  });
  return resultRoutes;
}
